ades de material en suspensión, o cuando deba efectuarse una medición del caudal en un período muy breve. Para lo cual se mide la velocidad superficial a lo ancho del cauce, utilizando flotadores especialmente diseñados y suministrados para este efecto.")]),_c('div',{staticClass:"col-lg-8 m-auto mt-5"},[_c('div',{staticClass:"titulo-sexto color-acento-botones"},[_c('h5',{staticClass:"d-block"},[_vm._v("Figura 4")]),_c('span',[_vm._v("Medición de la velocidad de flujo")])]),_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema3/36.png"),"data-aos":"zoom-in-up","alt":"Representa la medición de la velocidad de flujo"}}),_c('figcaption',{staticClass:"p-3 text-center fw-normal mt-4",staticStyle:{"background-color":"#fff"}},[_vm._v("Fuente: Protocolo de monitoreo y seguimiento del agua 2021")])])])]),_c('div',{attrs:{"numero":"6","titulo":"Aforo ADCP (<em>Acoustic Doppler Current Profiler</em>)"}},[_c('p',[_vm._v(" Se utiliza para medir el caudal en ríos grandes o pequeños, sin rocas grandes que permitan el desplazamiento horizontal del equipo para medir el caudal.")]),_c('p',{staticClass:"mt-4"},[_vm._v("Los instrumentos ADCP, basados en el efecto Doppler, pueden instalarse en una embarcación en movimiento. El instrumento mide simultáneamente la velocidad y profundidad del agua y la trayectoria de la embarcación para calcular el caudal. Este método permite calcular el caudal parcial a medida que la embarcación atraviesa el río. El resultado de una medición no es suficiente para proporcionar un valor exacto del flujo/caudal; únicamente proporciona una imagen instantánea del flujo. ")]),_c('p',{staticClass:"mt-4"},[_vm._v("Para conseguir un valor más exacto del caudal del río es importante calcular el promedio de varias travesías. Para calcular el caudal en un emplazamiento se recomienda efectuar, como mínimo, cuatro travesías (OMM, 2011).")]),_c('p',{staticClass:"mt-4"},[_vm._v("El ADCP utiliza el efecto Doppler, transmitiendo sonido a una frecuencia fija y esc